<template>
  <div id="body2">
    <div class="hotop">
      <img src="@/assets/1609298326(1).png" alt="" />
    </div>
    <div class="gq">
      <a href="javascript:;" v-for="(item, index) in Gqxq" :key="index">
        <div class="nr">
          <div class="left" :class="{ cur: index < 3 }">
            {{ index + 1 >= 10 ? index + 1 : "0" + (index + 1) }}
          </div>
          <div class="zj">
            <div class="nr-a">
              {{ item.name }}
              <span>{{ item.alia != "" ? "(" + item.alia[0] + ")" : "" }}</span>
            </div>
            <div class="nr-b">
              <img src="@/assets/1609241081.jpg" alt="" />
              <span v-for="(item1, index) in item.ar" :key="index"
                >{{ item1.name }}/</span
              ><span>-{{ item.al.name }}</span>
            </div>
          </div>
          <div id="right"></div>
        </div>
      </a>
    </div>
    <div class="bottom">
      查看完整榜单
    </div>
  </div>
</template>
<script>
// 先拿到歌单详情, 在榜单内容摘要里拿到热歌榜对应的id,把这个id给歌单详情传过去,在拿到所有的id,传到歌曲详情中拿到具体数据

export default {
  data() {
    return {
      rg: [], //获取到热歌榜里面的id
      id: [], //遍历之后的id
      idString: [], //转换成字符串的id,
      Gqxq: [], //歌单内容
    };
  },
  mounted() {
    this.getList();
  },
  methods: {
    getList() {
      this.$http.post("api/playlist/detail?id=3778678").then((res) => {
        this.rg = res.data.playlist.trackIds;
        // console.log(this.rg, "-------------------");
            // console.log("rrrrrggg--------",res);
        this.rg.forEach((item) => {
          if (this.id.length < 20) {
            this.id.push(item.id);
          }
        });
        this.idString = this.id.toString();//转换成字符串
        // console.log(this.idString, "-------------------");
        this.$http
          .post("api/song/detail?ids=" + this.idString + "")
          .then((res) => {
            this.Gqxq = res.data.songs;
          });
      });
    },
  },
};
</script>
<style lang="less">
#body2 {
  overflow: auto;
  height: 500px;
  .hotop {
    width: 100%;
    height: 146px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .gq {
    a {
      display: inline-block;
      width: 100%;
      height: 55px;
      box-sizing: border-box;
      .nr {
        width: 100%;
        height: 100%;
        border-bottom: 1px solid #ccc;
        color: #333;
        position: relative;
        .left {
          width: 40px;
          height: 100%;
          float: left;
          text-align: center;
          line-height: 58px;
          color: #888;
          &.cur {
            color: red;
          }
        }
        .zj {
          overflow: hidden;
          .nr-a {
            height: 25px;
            font-size: 16px;
            width: 293px;
            height: 25px;
            line-height: 25px;
            margin-top: 4px;
            span {
              color: #888;
            }
          }
          .nr-b {
            font-size: 12px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            width: 293px;
            height: 18px;
            color: #888;
            img {
              vertical-align: -5px;
            }
          }
        }
        #right {
          width: 23px;
          height: 23px;
          position: absolute;
          right: 15px;
          top: 50%;
          transform: translateY(-50%);
          background: url("../../assets/index_icon.jpg");
          background-position: 144px 0;
          background-size: 169px;
        }
      }
    }
  }
  .bottom {
    width: 100%;
    height: 55px;
    text-align: center;
    line-height: 55px;
    color: #888;
  }
}
</style>
