<template>
  <div>
    <div class="cantainer">
      <h2 class="title">
        推荐歌单
      </h2>
      <div class="remd_songs">
        <a
          href="javascript:;"
          v-for="(item, index) in songList"
          :key="index"
          @click="router(item.id)"
        >
          <img :src="item.picUrl" alt="" />
          <span>{{ (item.playCount / 10000).toFixed(1) }}万</span>
          <p>{{ item.name }}</p>
        </a>
      </div>
      <h2 class="title">
        最新音乐
      </h2>
      <div class="gq">
        <a
          href="javascript:;"
          v-for="(item, index) in zxye"
          :key="index"
          @click="gqbf(item.id, item.picUrl, item.name)"
        >
          <div class="nr">
            <div class="nr-a">{{ item.name }}</div>
            <div class="nr-b">
              <img src="@/assets/1609241081.jpg" alt="" />
              <span v-for="(item1, index) in item.song.artists" :key="index"
                >{{ item1.name }}/ {{ item.song.album.name }}</span
              >
            </div>
            <div id="right"></div>
          </div>
        </a>
      </div>
      <div class="ftwrap">
        <div class="log">
          <img src="@/assets/1609297143(1).jpg" alt="" />
        </div>
        <div class="dibu">
          打开APP，发现更多好音乐 >
        </div>
        <p>网易公司版权所有©1997-2020 杭州乐读科技有限公司运营</p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      songList: [], //推荐歌单列表
      zxye: [], //最新音乐
      img: [],
    };
  },
  mounted() {
    this.getSongList();
    this.getTopList();
  },
  methods: {
    getSongList() {
      this.$http.post("/api/personalized?limit=6").then((res) => {
        if (res.status == 200 && res.statusText == "OK") {
          this.songList = res.data.result;
        }
      });
    },
    getTopList() {
      this.$http.post("/api/personalized/newsong").then((res) => {
        // console.log(res);
        if (res.status == 200 && res.statusText == "OK") {
          this.zxye = res.data.result;
        }
        // console.log(this.zxye);
        // console.log(res.data.result);
      });
    },
    // 6首歌单对应id
    router(id) {
      let obj = { path: "/bof", query: { id } };
      this.$router.push(obj);
    },
    //
    gqbf(id, url, name) {
      let obj = { path: "/gqbf", query: { id, url, name } };
      this.$router.push(obj);
    },
  },
};
</script>
<style lang="less">
.cantainer {
  padding-top: 20px;
  padding-bottom: 24px;
  overflow: auto;
  height: 500px;
  .title {
    padding-left: 9px;
    font-weight: 100;
    color: #333;
    font-size: 17px;
    border-left: 2px solid red;
    margin-bottom: 20px;
  }
  .remd_songs {
    padding-bottom: 20px;
    a {
      display: inline-block;
      width: 33.3%;
      border-bottom: none;
      padding-right: 2px;
      box-sizing: border-box;
      position: relative;
      text-decoration: none;
      margin-bottom: 10px;
      img {
        width: 100%;
        height: 100%;
      }
      span {
        color: #fff;
        position: absolute;
        right: 5px;
        top: 5px;
        font-size: 12px;
      }
      p {
        padding: 6px 6px 0 6px;
        color: #333;
        overflow: hidden;
        width: 100%;
        height: 42px;
        font-size: 13px;
        box-sizing: border-box;
      }
    }
  }
  .gq {
    height: 551px;
    overflow: hidden;
    a {
      display: inline-block;
      width: 100%;
      height: 55px;
      padding-left: 10px;
      padding-top: 6px;
      box-sizing: border-box;
      .nr {
        width: 100%;
        height: 100%;
        border-bottom: 1px solid #ccc;
        color: #333;
        position: relative;
        .nr-a {
          height: 25px;
          font-size: 16px;
        }
        .nr-b {
          font-size: 12px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          width: 340px;
          color: #888;
          img {
            vertical-align: -5px;
          }
        }
        #right {
          width: 22px;
          height: 22px;
          position: absolute;
          right: 15px;
          top: 50%;
          transform: translateY(-50%);
          background: url("../../../../assets/index_icon.jpg");
          background-position: 144px 0;
          background-size: 169px;
        }
      }
    }
  }
  .ftwrap {
    width: 100%;
    height: 200px;
    background: url("../../../../assets/recommand_bg_2x.png");
    margin-top: 5px;
    text-align: center;
    .log {
      padding-top: 16.9%;
    }
    .dibu {
      line-height: 38px;
      border: 1px solid #d33a31;
      border-radius: 38px;
      font-size: 16px;
      color: #d33a31;
      margin: 15px 37px 5px;
    }
    p {
      color: #888;
      font-size: 12px;
      line-height: 16px;
    }
  }
}
</style>
