<template>
  <div>
    <div class="top">
      <form class="m-input">
        <img src="@/assets/1609719066(1).png" alt="" />
        <input
          type="text"
          placeholder="搜索歌曲、歌手、专辑"
          style="color:'#c9c9c9'"
          v-model="ss"
          @keyup="add"
        />
        <div class="img2" v-show="ss !== ''" @click="dele">
          <img src="@/assets/1609759256(1).png" alt="" />
        </div>
      </form>
    </div>
    <div class="box" v-show="ss == ''">
      <h3>热门搜索</h3>
      <ul class="list">
        <li
          v-for="(item, index) in dataList"
          :key="index"
          @click="z(item.first)"
        >
          {{ item.first }}
        </li>
      </ul>
    </div>
    <div class="gq" v-show="ss !== ''">
      <a href="javascript:;" v-for="(item, index) in songList" :key="index">
        <div class="nr">
          <div class="nr-a">{{ item.name }}</div>
          <div class="nr-b">
            <img src="@/assets/1609241081.jpg" alt="" />
            <span>{{ item.artists[0].name }}/{{ item.album.name }}</span>
          </div>
          <div id="right"></div>
        </div>
      </a>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      dataList: {}, //热门搜索部分
      ss: "",
      songList: [], //相似歌曲
    };
  },
  mounted() {
    this.getList();
  },
  methods: {
    getList() {
      this.$http.post("api/search/hot").then((res) => {
        // console.log(res);
        let list = res.data.result.hots;
        this.dataList = list;
      });
    },
    z(sj) {
      this.ss = sj;
      this.soList();
      console.log("sj",sj)
    },
    soList() {
      this.$http.post("search?keywords=" + this.ss + "").then((res) => {
        let list = res.data.result.songs;
        this.songList = list;
        console.log(this.songList, "------------------***");
      });
    },
    add() {
      this.soList();
      console.log(this.ss);
    },
    dele() {
      this.ss = "";
    },
  },
};
</script>
<style lang="less">
.top {
  height: 60px;
  padding: 15px 10px;
  box-sizing: border-box;
  border-bottom: 1px solid#ebecec;
  form {
    width: 100%;
    height: 100%;
    background: #ebecec;
    border-radius: 30px;
    padding: 0 30px;
    box-sizing: border-box;
    position: relative;
    input {
      height: 100%;
      position: absolute;
      z-index: 999;
      left: 30px;
      border: none;
      background: #ebecec;
    }
    img {
      width: 20px;
      height: 20px;
      margin: 0 8px;
      margin-top: 6px;
      position: absolute;
      left: 0;
    }
    .img2 {
      width: 20px;
      height: 20px;
      position: absolute;
      right: 15px;
      top: -2px;
    }
    label {
      position: absolute;
      left: 30px;
      top: 5px;
      font-size: 14px;
      color: #c9c9c9;
    }
  }
}

.box {
  padding: 15px 10px 0;
  h3 {
    height: 12px;
    font-size: 12px;
    font-weight: 500;
    line-height: 12px;
    color: #666;
  }
  .list {
    margin: 10px 0 7px;
    li {
      display: inline-block;
      height: 32px;
      margin-right: 8px;
      margin-bottom: 8px;
      padding: 0 14px;
      font-size: 14px;
      line-height: 32px;
      color: #333;
      border-radius: 50px;
      border: 1px solid #ebecec;
    }
  }
}
.gq {
  height: 400px;
  overflow: auto;
  a {
    display: inline-block;
    width: 100%;
    height: 55px;
    padding-left: 10px;
    padding-top: 6px;
    box-sizing: border-box;
    .nr {
      width: 100%;
      height: 100%;
      border-bottom: 1px solid #ccc;
      color: #333;
      position: relative;
      .nr-a {
        height: 25px;
        font-size: 16px;
      }
      .nr-b {
        font-size: 12px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        width: 340px;
        color: #888;
        img {
          vertical-align: -5px;
        }
      }
      #right {
        width: 22px;
        height: 22px;
        position: absolute;
        right: 15px;
        top: 50%;
        transform: translateY(-50%);
        background: url("../../assets/index_icon.jpg");
        background-position: 144px 0;
        background-size: 169px;
      }
    }
  }
}
.gd {
  width: 100%;
  height: 45px;
  padding-left: 10px;
  color: #333;
  li {
    height: 45px;
    border-bottom: 1px solid #ccc;
  }
  img {
    margin-top: 10px;
    vertical-align: -3px;
  }
}
</style>
